<template>
    <div class="pa-4 d-flex justify-center">
        <h1>Home page</h1>

        <v-btn @click="get" CLASS="pink pa-3">
            MEGA-ABOBES
        </v-btn>
    </div>
</template>

<script>
import api from '@/api';

export default {
    data() {
        return {
            
        }
    },
    methods: {
        get() {
            api.get('/api/ButtonClicked') 
            .then(response => {
            console.log(response.data);
            })
            .catch(error => {
            alert(error);
            });
        },
        post() {
            const data = { name: 'John Doe', email: 'john@example.com' };

            api.post('/api/createdata', data)
            .then(response => {
                console.log('Data created:', response.data);
            })
            .catch(error => {
                console.error('Error creating data:', error);
            });
        },
        put() {
            const data = { id: 1, name: 'Jane Doe', email: 'jane@example.com' };

            api.put(`/api/updatedata/${data.id}`, data)
            .then(response => {
                console.log('Data updated:', response.data);
            })
            .catch(error => {
                console.error('Error updating data:', error);
            });
        },
        delete() {
            const id = 1;

            api.delete(`/api/deletedata/${id}`)
            .then(response => {
                console.log(response);
            })
            .catch(error => {
                console.error('Error deleting data:', error);
            });
        }

    },

}
</script>